<template>
    <view class="bind-ticket">
        <view class="ticket-header">
            {{ticketName}}
        </view>        
        <view class="ticket-body">
            <view class="item">
                <text class="lable">姓名</text>
                <input type="text" placeholder="请输入姓名" v-model.trim="name">
            </view>
            <view class="item" @click="showWarning">
                <text class="lable">头像上传</text>
                <view class="loading" v-if="imgUploading">上传中。。。</view>
                <img :src="headerImg" class="header-img" v-else-if="headerImg">
                <view class="icon-arrow">上传</view>
            </view>
        </view>
        <view class="ticket-info">
            <view class="item">
                <text class="lable">手机号码</text>
                <text class="value">{{userInfo.phone | phoneText}}</text>
            </view>
            <view class="item">
                <text class="lable">联票编码</text>                
                <input type="text" placeholder="请输入联票编码" v-model.trim="ticketCode" @blur="handlerCodeBlur($event)">
            </view>
            <view class="item">
                <text class="lable">密码</text>
                <input type="text" placeholder="请输入联票密码" v-model.trim="ticketPassword" @blur="handlerPassword($event)">
            </view>
        </view>
        <view class="confirm-btn-group">
            <view class="confirm-btn"  @click="bindTicket">
                确认绑定            
            </view>
            <view class="scan-btn" @click="scanBind" v-if="showScanBtn">
                扫码绑定
            </view>
            <view class="tip">
                <p>1 联票一经使用，绑定信息将不能被更改</p>
                <p>2 确认上传为本人单人正面照</p>
                <p>3 非照片本人使用会被拒绝接待</p>
            </view>
        </view>
    </view>
</template>

<script>
import { mapState } from 'vuex' 
import { upImgUrl, _shop_type } from '../../libs/envLib'
export default {
    data() {
        return {
            ticketCode: '',
            ticketPassword: '',
            imgUploading: false,
            headerImg: '',
            forbidClick: false,
            name: '',
            ticketName: '',
            showScanBtn: true,
            bindPageFromOut: false //是否是从别的小程序跳转到的bindTicket页面
        }
    },
    filters: {
        phoneText(val) {
            let arr = val ? val.split('') : []
            return `${arr.splice(0,3).join('')}****${arr.splice(4,4).join('')}`
        }
    },
    onLoad(option) {
        console.log(option)
        this.ticketCode = option.code || ''
        this.ticketPassword = option.password || ''
        this.ticketName = option.name || ''
        this.bindPageFromOut = !option.from
        if (option.code) {
            this.showScanBtn = false
        }
    },
    onShow() {
        this.hasLogin()
    },
    computed: {        
        ...mapState(['ticketBaseInfo','userInfo']),
    },
    methods: {
        hasLogin () {
            if (!this.userInfo) {
                // 如果未登录就到了绑定页只有可能是有赞跳入，多数场景为未注册用户
                this.$tip.toast('请先注册，成为会员','none')
                uni.reLaunch({
                    url: '../signUp/signUp?from=bindpage&page=signUp',
                });
            } else {
                this.checkoutClipboard()
            }
        },
        checkoutClipboard () {
            let that = this
            let preTicket = uni.getStorageSync('ticketFromOut') 
            uni.getClipboardData({
                success: function (res) {
                    console.log('剪贴板获取成功', res)     
                    let str = res.data 
                    // 长度为17,";"在第十位，"a"在第一位大概率为联票的复制信息
                    if (str.length === 17 && 
                    str.indexOf('a') === 0 && 
                    (str.indexOf(';') === 10 || str.indexOf('；') === 10)) {
                        let currentTicket = str.split(';')[0]
                        let currentPassword = str.split(';')[1]
                        if (currentTicket === preTicket && !that.bindPageFromOut) return
                        that.getUserTicketList(currentTicket)                    
                        that.ticketCode = currentTicket
                        that.ticketPassword = currentPassword   
                        uni.setStorageSync('ticketFromOut', currentTicket)               
                    }
                }
            })
        },
        async getUserTicketList (currentTicket) {
            let params = {
                pageNum: 1,
                pageSize: 100,
                username: this.userInfo.username
            }
            const res = await this.$api.bindTicketList(params)
            if (res.code === '0') {
                if (res.data.list.length > 0) {
                    let temp = res.data.list.find((el) => {
                        return el.childCode === currentTicket
                    })
                    if (temp) {
                        this.$tip.alertDialog(
                        '您复制的联票信息已绑定',
                        '前去使用','继续绑定')
                        .then(() => {
                            uni.reLaunch({
                                url: `../checkIn/checkIn?ticket=${currentTicket}`,
                            });
                        }).catch(() => {                                
                            this.ticketCode = ''
                            this.ticketPassword = '' 
                        }) 
                    } else {                           
                        this.$tip.toast('已填充您的联票信息','none')
                    }
                }
            }
        },
        handlerCodeBlur (e) {
            this.ticketCode = e.target.value
        },
        handlerPassword (e) {
            this.ticketPassword = e.target.value
        },
        bindTicket() {
            if (this.forbidClick) return
            if (!this.checkForm()) return
            this.forbidClick = true
            setTimeout(() => {
                this.sendData()
            },200)            
        },
        scanBind () {    
            let that = this        
            uni.scanCode({
                onlyFromCamera: true,
                scanType: 'QR_CODE',
                success (res) {
                    that.postScanData(res.result)
                },
                fail () {
                    that.$tip.toast('扫描失败,请重试','none')
                }
            })
        },
        postScanData (val) {
            let temp = null
            console.log(val)
            try {
                temp = JSON.parse(val)
            } catch (error) {
                this.$tip.alertDialog('请扫描正确的联票二维码')
                return
            }
            if (temp && temp.isLEXING) {
                this.ticketCode = temp.ticketCode
                this.ticketPassword = temp.ticketPassword
                    this.$tip.toast('联票添加成功','none')
            } else {                
                this.$tip.alertDialog(`非${_shop_type === 'lx' ? '乐行' : '欢游'}公司发行的联票`)
            }
        },
        async sendData () {
            let params = {
                bindingName: this.name,
                bingingPhoto: this.headerImg,
                childCode : this.ticketCode,
                password : this.ticketPassword,
                username: this.userInfo.username
            }
            const res = await this.$api.bindTicket(params)
            if (res.code === '0') { 
                this.$tip.alertDialog(
                '绑定成功',
                '前去使用','继续绑定').then(() => {
                    uni.reLaunch({
                        url: `../checkIn/checkIn?ticket=${this.ticketCode}`,
                    });
                }).catch(() => {                                
                    this.ticketCode = ''
                    this.ticketPassword = '' 
                    this.name = '' 
                    this.headerImg = '' 
                })              
            } else {
                this.forbidClick = false
                this.$tip.toast(res.message,'none')
            }
        },
        checkForm () {
            if (this.name === '') {
                this.$tip.toast('请输入使用者名称','none')
                return false
            }
            if (this.headerImg === '') {
                this.$tip.toast('请上传头像','none')
                return false
            }
            if (this.ticketCode === '') {
                this.$tip.toast('请输入联票编号','none')
                return false
            }
            if (this.ticketPassword === '') {
                this.$tip.toast('请输入联票密码','none')
                return false
            }
            if (this.imgUploading) {
                this.$tip.toast('请等待头像上传完成','none')
                return false
            }
            return true
        },
        showWarning () {
            this.$tip.confirm('上传单人大头照，仅限照片本人使用，合照不可使用')
            .then((res) => {
                this.uploadImg()
            })
            .catch(() => {
                return
            })
        },
        uploadImg () {
            let that = this
            wx.chooseImage({
                count: 1,
                sizeType: ['compressed'],
                sourceType: ['album','camera'],
                success (res) {
                    const tempFilePaths = res.tempFilePaths
                    that.imgUploading = true
                    console.log(res)
                    wx.uploadFile({
                        url: upImgUrl,
                        filePath: tempFilePaths[0],
                        name: 'file',
                        success (res1) {
                            let temp = JSON.parse(res1.data)
                            if (temp.code === '0') {  
                                that.headerImg = temp.data
                                that.$tip.toast('头像上传成功','none')
                            } else {
                                that.$tip.toast(temp.message,'none')
                            }
                        },
                        complete () {
                            setTimeout(() => {                                        
                                that.imgUploading = false
                            }, 1000);                    
                        }
                    })
                }
            })
        }
    }
}
</script>

<style>
.bind-ticket {
    width: 100%;
    height: 100vh;
    background: #F3F3F3;
}
.ticket-header {
    text-align: center;
    height: 50upx;
    line-height: 50upx;
    font-size: 13px;
    color: #000000;
}
.ticket-body,.ticket-info {
    font-size: 28upx;
    background: #fff;
    padding-left: 14upx;
    padding-right: 14upx;
    margin-bottom: 20upx;
}
.ticket-body {
    color: #000;
}
.ticket-info {
    color: #9A9A9A;
    margin-bottom: 76upx;
}
.item + .item {
    border-top: 1px solid #eee;
}
.ticket-info .item,
.ticket-body .item {    
    width: 100%;
    padding-left: 5px;
    padding-right: 5px;
    display: flex;
    align-items: center;
    height: 90upx;
}
.item .lable {
    width: 159upx;
}
.item .value,
.item .icon-arrow {
    margin-left: auto;
}
.confirm-btn-group {
    position: relative;
    display: flex;
    justify-content: space-around;
    width: 100%;
    height: 74upx;
}
.confirm-btn,.scan-btn {    
    width: 305upx;
    font-size: 16px;
    line-height: 74upx;
    text-align: center;
    color: #fff;
    border-radius: 6px;
    background: linear-gradient(-53deg,rgba(255,144,14,1),rgba(255,204,0,1));;
}
.icon-arrow {    
    color: #9A9A9A;
}
.icon-arrow::after {
    content: '';    
    display: inline-block;
    width: 6px;
    height: 6px;
    margin-left: 6px;
    border: 1px solid #888;
    transform: rotate(45deg);
    border-left: transparent;
    border-bottom: transparent;
}
.confirm-btn-group .tip {
    position: absolute;
    bottom: -70px;
    font-size: 14px;
    color: #888;
    text-align: left;
}
.header-img {
    width: 60upx;
    height: 60upx;
}
</style>